/*
 *   Name: Bizagi Desktop Render fileprint Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the fileprint render class to adjust to desktop devices
 */

// Extends from render Desktop base
bizagi.rendering.desktop.render.extend("bizagi.rendering.desktop.fileprint", bizagi.rendering.fileprint.prototype);
// Extends itself
bizagi.rendering.desktop.fileprint.extend("bizagi.rendering.desktop.fileprint", {}, {

  /**
                                                                                       * Template method to implement in each device to customize each render after processed
                                                                                       */
  postRender: function postRender() {
    var button = $(":button", this.getControl());

    // Stylize button
    button.button();
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var properties = self.properties;
    var button = $(":button", self.getControl());

    // Call base
    self._super();

    if (properties.filesCount > 0) {
      // Bind event
      button.click(function () {
        // Process button actions
        self.processButton();
      });
    } else {
      button.button("option", "disabled", "true");
    }
  },

  /**
      * Method to render non editable values
      * @returns {}
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var self = this;
    var form = self.getFormContainer();
    var typeForm = form.properties.typeForm;

    // Execute the same as post-render
    this.postRender();

    // Set as disabled
    var button = $(":button", this.getControl());

    if (typeForm == "SummaryForm" || typeForm == "GlobalForm") {
      self.configureHandlers();
    } else {
      button.button("option", "disabled", "true");
    }
  },

  /**
      * Process the button actions workflow
      */
  processButton: function processButton() {
    var self = this;
    var properties = self.properties;
    var form = self.getFormContainer();
    var typeForm = form.properties.typeForm;
    if (properties.editable || typeForm == "SummaryForm" || typeForm == "GlobalForm") {
      self.runButtonAction();
    }
  },

  /**
      * This url must be absolute, in portal and sharepoint not apply relative
      * @param {} fileType
      */
  getAbsoluteFilePrintUrl: function getAbsoluteFilePrintUrl(fileType) {
    var time = new Date().getTime();
    var filePrintUrl = this.getFilePrintUrl(fileType);

    if (filePrintUrl.indexOf('http') != -1)
    return filePrintUrl + "&_=" + time;

    filePrintUrl = filePrintUrl.replace(bizagi.services.ajax.pathToBase, "");
    filePrintUrl = bizagiConfig.proxyPrefix + filePrintUrl + "&_=" + time;

    if (bizagi.getXSRFToken() != null)
    filePrintUrl += "&xsrf=" + bizagi.getXSRFToken();

    return filePrintUrl;
  },

  runButtonAction: function runButtonAction() {
    var self = this;
    self.getFormContainer().clearValidationMessages();

    var control = self.getControl();
    var fileObject = $(".fileobj");

    if (bizagi.util.isIE() && !bizagi.util.isIE10() && !bizagi.util.isIE11()) {

      var isAcrobatInstalled = self.checkAdobePlugin();

      if (!isAcrobatInstalled) {
        $.when(bizagi.showMessageBox(bizagi.localization.getResource("workportal-acrobat-message"))).
        done(function () {
          window.open(self.getAbsoluteFilePrintUrl("inline"));
        });
      } else {
        var template = "";
        var counter = 0;
        var message = bizagi.localization.getResource("webpart-render-loading");

        fileObject.remove();
        template = self.renderFactory.getTemplate("fileprint-object");

        control.append("<label id='loadingMsg'> " + message + "</label>");
        control.append($.tmpl(template, { url: self.getAbsoluteFilePrintUrl("inline") }));

        var mycounter = setInterval(function () {

          if (document.all["fileobj"] && document.all["fileobj"].readyState > 0) {

            $("#loadingMsg").remove();
            $(".fileobj:last", control).focus();

            if (typeof $(".fileobj:last", control)[0].print != "undefined") {
              $(".fileobj:last", control)[0].print();
            } else {
              window.open(self.getAbsoluteFilePrintUrl("attachment"));
            }

            window.clearInterval(mycounter);
          } else if (counter >= 10) {

            $("#loadingMsg").remove();

            $.when(bizagi.showMessageBox(bizagi.localization.getResource("workportal-print-too-slow"))).
            done(function () {
              var wnd = window.open(self.getAbsoluteFilePrintUrl("inline"));

              wnd.onload = function () {
                if (typeof wnd.print != "undefined") {
                  wnd.print();
                  wnd.close();
                } else {
                  window.open(self.getAbsoluteFilePrintUrl("attachment"));
                }
              };
            });

            self.saveForm();
            window.clearInterval(mycounter);
          }
          counter += 1;
        }, 1000);
      }
    } else {

      if ($.browser.mozilla) {
        window.open(self.getAbsoluteFilePrintUrl("attachment"));
      } else {
        // Remove previous iframe
        $("iframe", control).remove();

        // Render new iframe
        var idFrame = "bz-render-fileprint-iframe-" + new Date().getTime();
        var tmpl = self.renderFactory.getTemplate("fileprint-iframe");
        var iframe = $.tmpl(tmpl, { url: self.getAbsoluteFilePrintUrl("inline"), id: idFrame });

        control.append(iframe);

        // Print iframe
        var frm = document.getElementById(idFrame).contentWindow;
        if (typeof frm.print != "undefined" && !bizagi.util.isIE()) {
          frm.print();
        } else {
          window.open(self.getAbsoluteFilePrintUrl("attachment"));
        }
      }
    }
  },

  checkAdobePlugin: function checkAdobePlugin() {
    var installed;

    try {
      installed = new ActiveXObject('AcroPDF.PDF');
    } catch (e) {
      bizagi.log(e);
    }

    if (!installed) {
      try {
        // Older IE
        installed = new ActiveXObject('PDF.PdfCtrl');
      } catch (e) {
        bizagi.log(e);
      }
    }

    return installed;
  } });